@import './_variables'
@import './_extensional'

.container-book
    @include container(900px, 100vh, center, row)
    @media (max-width: 780px)
        height: auto

.book--wrapper
    @include container(100%, auto, start, column)
    @media (min-width: 780px) and (max-width: 910px)
        width: 95%
    @media  (max-width: 779px)
        width: 90%
        align-items: center
    div
        width: 100%
        @media (max-width: 780px)
            width: 90%
            display: flex
            align-items: center

    h1
        font-family: $fontText
        color: $blackText
        margin-bottom: 15px
        font-weight: 900
        font-size: 20px

.book--form
    width: 100%
    display: grid
    grid-template-columns: 1fr 1fr 1fr
    grid-gap: 40px

    @media (max-width: 780px)
        grid-template-columns: 1fr

    div
        @include container(200px, auto, start, column)
        align-items: start
        @media (max-width: 780px)
            width: 100%
        label
            font-family: $fontText
            margin-bottom: 10px

            img
                width: 22px
                margin-right: 10px
            .title-car
                color: $blackText

        select, input
            -webkit-appearance: none
            appearance: none
            font-family: $fontText
            color: $greyColor
            padding: 5px 20px
            width: 235px
            border: none
            border-left: 2px solid $greyColor
            border-bottom: 2px solid $greyColor
            background: transparent
            border-radius: 0
            @media (max-width: 780px)
                width: 100%
                -webkit-appearance: none
                appearance: none

        select:-webkit-autofill
            background-color: transparent
            color: $greyColor

        input
            width: 200px
            @media (max-width: 780px)
                width: calc(100% - 42px)
        //     -webkit-appearance: none
        //     appearance: none
        //     font-family: $fontText
        //     color: $greyColor
        //     padding: 5px 20px
        //     width: 200px
        //     border: none
        //     border-left: 2px solid $greyColor
        //     border-bottom: 2px solid $greyColor
        //     background: transparent
        //     border-radius: 0
        //     @media (max-width: 780px)
        //         width: calc(100% - 42px)
        //         -webkit-appearance: none
        //         appearance: none
        // input:-webkit-autofill

        //     background-color: transparent
        //     color: $greyColor

    button
        background-color: $btmBrandColor
        margin-right: 15px
        box-shadow: 2px 3px 8px #f14303
        width: 235px
        border: none
        size: 20px
        padding: 10px 40px
        border-radius: 2px
        font-family: $fontText
        color: $whiteColor
        cursor: pointer
        @media (max-width: 780px)
            width: 100%

.book--form--input
    width: 200px
    @media (max-width: 780px)
        width: 200px

// MapOfBook section:

.book--map
    @include container(100%, auto, center, column)
    p
        font-family: $fontText
        font-size: 15px
        color: $blackText
        margin-bottom: 15px
        font-weight: 500
    h2
        font-family: $fontText
        color: $blackText
        margin-bottom: 60px
        font-weight: 900
    div
        @include container(100%, auto, space-between, row)
        @media  (min-width: 40px) and (max-width: 500px)
            flex-direction: column

        .book--map--item
            @include container(220px, auto, space-around, column)
            @media  (min-width: 501px) and (max-width: 700px)
                margin: 0 30px
                width: 110px
                img
                    width: 20px
            @media  (min-width: 701px) and (max-width: 930px)
                margin: 0 30px
            img
                width: 50px
                margin-bottom: 10px
            h3
                margin-bottom: 10px
                font-family: $fontText
                color: $blackText
                text-shadow: 0 0 2px #ff000059, 0 0 1px #ff3e151f
                margin-bottom: 15px
            p
                text-align: center
                color: $greyColor
                font-size: 10px
